@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
  background-color: #dfeefc;
  display: grid;
  height : 4em;
  padding: 2%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  grid-template-areas: "myArea myArea";
  
}
.mobileList{
  text-align: center;
  grid-row-gap: 20px;
  grid-template-areas: "a";
}
header.dropNav{
  grid-template-areas: "myArea";
  background-color: rgb(0,0,0,0);
  
  padding: 0%;
  box-shadow: none;
}
header.dropNav > div{
  background-color: #dfeefc;
  display: grid;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height : 4em;
  padding:2%;
  display: grid;
  grid-template-areas: "a a";
}
header.dropNav > div > button{
  text-align: right;
  background-color: rgb(0,0,0,0);
  border: 0;
}
header.dropNav > ul{
  background: rgba(129, 82, 204, 1  ) ;
}
.hideThis{
  display: none;
}
ul{
  list-style-type: none;
}
li > a{
  text-decoration: none;
  font-size: 1.4em;
}

.desktopList{
  display :grid;
  grid-template-areas: "a a a a a";
}
#section-banner{
    background-image: url("https://www.pixelstalk.net/wp-content/uploads/2015/07/Country-Fence-Wallpaper-Desktop-Full-HD-620x465.jpg");
    background-size: 100%;
    
}
.mystyle{
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 20px;
}
.req {
  border-width: 1px;
  border-style: solid;
  border-radius: 15px;
  height: 49%;
  width: auto;
}

